
import React, {Component} from "react";
import {connect} from "react-redux";
import OnlineUtils from "eshop/utils/OnlineUtils";
import OraTooltip from "eshop/modules/core/components/ui/Tooltip"
const SingleColumnLine = (props) =>{
    return (
    <div className={"l-row "+props.className}>
                    <div className="l-col l-col-12 ">
                        {props.col1}
                    </div>
                </div>
    )
}
const DoubleColumnLine = (props) =>{
    return (
        <div className="l-row">
            <div className="l-col l-col-7">
                {props.col1}
            </div>
            <div className="l-col l-col-5 u-text-right">
                {props.col2}
            </div>
        </div>
    )
}
class ProductOfferInstalmentPriceTooltipComponent extends Component{
    constructor(props){
        super(props);
    }
    render(){
        if(!this.props.instalmentTooltipDescription || this.props.instalmentTooltipDescription=="")
            return (null)
        var proposition=this.props.proposition;

        if(!proposition)return (null)
        var basePrice = this.props.proposition.deviceData.inOffer.price.base
        var convergentPrice = this.props.proposition.deviceData.inOffer.price.convergent

        var oneTimePayment = OnlineUtils.formatPrice(this.props.proposition.deviceData.inOffer.price.base.oneTimePayment)
        var deviceRecurringPayments = basePrice.devicePayments[0]
        var deviceMonthlyPayment = OnlineUtils.formatPrice(deviceRecurringPayments.price)
        var devicePaymentsLength = deviceRecurringPayments.monthTo;

        var devicePayment=OnlineUtils.formatPrice(this.props.clientContext?convergentPrice.devicePayments[0].price:basePrice.devicePayments[0].price);

        if(!devicePayment){
            return (<p>empty</p>)
        }
        var rows = [];
        var i=0;

        rows.push(<SingleColumnLine key={"line-"+(i+4)} col1={"Rata za urządzenie"} />);
        rows.push(<DoubleColumnLine key={"line-"+(i+5)} col1={"od 1 do "+devicePaymentsLength+" mies."} col2={devicePayment + " zł/mies."}/>);
        rows.push(<div key={"line-"+(i+6)} className="l-row  u-padding-all-m"><div className="o-separator o-separator--s o-separator--full-width "></div></div>);
        rows.push(<SingleColumnLine key={"line-"+(i+7)} col1={this.props.instalmentTooltipDescription} className={"u-font-bold u-font-small"}/>);
        return (<OraTooltip maxWidth={400} baseId="instalmentTooltip">{rows}</OraTooltip>)
    }
}
export default ProductOfferInstalmentPriceTooltipComponent;

